// frontend/lib/api.ts

import axios, { AxiosError, InternalAxiosRequestConfig } from 'axios';

interface CustomAxiosRequestConfig extends InternalAxiosRequestConfig {
  _retry?: boolean;
}

const isServer = typeof window === 'undefined';

const API_URL = isServer
  ? (process.env.INTERNAL_API_URL || 'http://backend:8000/api') 
  : (process.env.NEXT_PUBLIC_API_URL || 'http://127.0.0.1:8085/api');

const BACKEND_URL = API_URL.replace(/\/api\/?$/, '');

const api = axios.create({
  baseURL: API_URL,
  withCredentials: true, // Required to send/receive cookies
  withXSRFToken: true,    // Automatically sends X-XSRF-TOKEN header
  headers: {
    'Accept': 'application/json',
    'Content-Type': 'application/json',
  },
});

export const initializeCsrf = async () => {
  // Use raw axios to avoid interceptor loops during the handshake
  return await axios.get(`${BACKEND_URL}/sanctum/csrf-cookie`, {
    withCredentials: true,
  });
};

api.interceptors.response.use(
  (response) => response,
  async (error: AxiosError) => {
    const originalRequest = error.config as CustomAxiosRequestConfig;
    
    // Handle 419 (Token Mismatch) by re-initializing CSRF once
    if (error.response?.status === 419 && !originalRequest._retry) {
      originalRequest._retry = true; 
      await initializeCsrf();
      return api(originalRequest);
    }

    // Redirect to login on 401 Unauthorized
    if (error.response?.status === 401 && !isServer) {
      if (!window.location.pathname.includes('/auth/sign-in')) {
         window.location.href = '/auth/sign-in'; 
      }
    }

    return Promise.reject(error);
  }
);

export const checkAuth = async () => {
  const { data } = await api.get('/user');
  return data.data; 
};

export default api;