// frontend/lib/validations/auth.ts

import * as z from "zod";

/**
 * âš¡ Login Validation Schema
 * Used for the initial "Command Access" screen.
 */
export const loginSchema = z.object({
  email: z
    .string()
    .min(1, { message: "Email is required" })
    .email({ message: "Must be a valid email address" }),
  password: z
    .string()
    .min(1, { message: "Password is required" }),
});

/**
 * âš¡ 2FA OTP Validation Schema
 * Used for the secondary verification step.
 */
export const otpSchema = z.object({
  code: z
    .string()
    .length(6, { message: "OTP must be exactly 6 digits" })
    .regex(/^\d+$/, { message: "OTP must only contain numbers" }),
});

// Derive TypeScript types from the schemas
export type LoginFormData = z.infer<typeof loginSchema>;
export type OtpFormData = z.infer<typeof otpSchema>;