"use client";

import { useRouter, useSearchParams } from "next/navigation";

import { LoginFormData } from "@/lib/validations/auth";
import { auth } from "@/lib/auth";
import { useState } from "react";

export function useAuthLogic() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const callbackUrl = searchParams.get("callbackURL") || "/dashboard";

  const [step, setStep] = useState<"login" | "2fa">("login");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  
  const [otp, setOtp] = useState("");
  const [userId, setUserId] = useState<string | null>(null); // Store ID for 2FA

  /**
   * 1. INITIAL LOGIN HANDLER
   */
  const handleLogin = async (data: LoginFormData) => {
    setLoading(true);
    setError(null);

    try {
      const res = await auth.login(data);

      // Check if 2FA is required based on your Laravel AuthController response
      if (res.requires_2fa) {
        setUserId(res.user_id); // Save user_id for the verify2FA call
        setStep("2fa");
        setLoading(false);
        return;
      }

      // Success Flow (No 2FA)
      // auth.login internally handles session creation for non-2FA users if they receive a token
      router.push(callbackUrl);

    } catch (err: any) {
      const msg = err.response?.data?.message || err.message || "Authentication failed";
      setError(msg);
    } finally {
      setLoading(false);
    }
  };

  /**
   * 2. OTP SUBMISSION HANDLER
   */
  const handleOtpSubmit = async (e?: React.FormEvent) => {
    if (e) e.preventDefault();
    
    if (!otp || otp.length < 6) {
      setError("Please enter a valid 6-digit code.");
      return;
    }

    if (!userId) {
      setError("Session expired. Please log in again.");
      setStep("login");
      return;
    }

    setLoading(true);
    setError(null);

    try {
      await auth.verify2FA({
        user_id: userId,
        code: otp
      });

      // auth.verify2FA internally handles setCookie and localStorage
      router.push(callbackUrl);
    } catch (err: any) {
      const msg = err.response?.data?.message || "Invalid authentication code.";
      setError(msg);
    } finally {
      setLoading(false);
    }
  };

  return {
    step,
    setStep,
    loading,
    error,
    otp,
    setOtp,
    handleLogin,
    handleOtpSubmit,
  };
}