// frontend/lib/auth.ts

import api, { initializeCsrf } from "./api";
import { deleteCookie, setCookie } from "cookies-next";

export interface LoginCredentials {
  email: string;
  password: string;
}

export interface TwoFactorPayload {
  user_id: string;
  code: string;
}

export const auth = {
  async login(credentials: LoginCredentials) {
    await initializeCsrf();
    const { data } = await api.post('/login', credentials);
    return data.data; 
  },

  async verify2FA(payload: TwoFactorPayload) {
    const { data } = await api.post('/login/verify-2fa', payload);
    
    const token = data.data.token;
    const context = data.data.context;

    // Set cookies with 'lax' to match backend SESSION_SAME_SITE_COOKIE
    setCookie("hive_token", token, { maxAge: 60 * 60 * 24 * 7, sameSite: 'lax' });
    setCookie("hive_context", context, { maxAge: 60 * 60 * 24 * 7, sameSite: 'lax' });
    
    if (typeof window !== "undefined") {
        localStorage.setItem("hive_token", token);
        localStorage.setItem("user_data", JSON.stringify(data.data.user));
    }
    
    return data.data;
  },

  async logout() {
    try {
      await api.post('/logout');
    } finally {
      deleteCookie("hive_token");
      deleteCookie("hive_context");
      localStorage.removeItem("hive_token");
      localStorage.removeItem("user_data");
      window.location.href = "/auth/sign-in";
    }
  }
};